@import url("https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Inter:wght@300;400;500;600&family=JetBrains+Mono:wght@400;500&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family:
    "Inter",
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    "Roboto",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code,
.font-mono {
  font-family:
    "JetBrains Mono", source-code-pro, Menlo, Monaco, Consolas, monospace;
}

h1,
h2,
h3,
h4,
h5,
h6,
.font-heading {
  font-family: "Space Grotesk", sans-serif;
}

@layer base {
  :root {
    --background: 0 0% 100%;
    --foreground: 0 0% 3.9%;
    --card: 0 0% 100%;
    --card-foreground: 0 0% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 0 0% 3.9%;
    --primary: 0 0% 9%;
    --primary-foreground: 0 0% 98%;
    --secondary: 0 0% 96.1%;
    --secondary-foreground: 0 0% 9%;
    --muted: 0 0% 96.1%;
    --muted-foreground: 0 0% 45.1%;
    --accent: 217 91% 60%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 0 0% 89.8%;
    --input: 0 0% 89.8%;
    --ring: 217 91% 60%;
    --radius: 0.375rem;
  }

  .dark {
    --background: 240 10% 3.9%;
    --foreground: 0 0% 98%;
    --card: 240 10% 3.9%;
    --card-foreground: 0 0% 98%;
    --popover: 240 10% 3.9%;
    --popover-foreground: 0 0% 98%;
    --primary: 0 0% 98%;
    --primary-foreground: 240 5.9% 10%;
    --secondary: 240 3.7% 15.9%;
    --secondary-foreground: 0 0% 98%;
    --muted: 240 3.7% 15.9%;
    --muted-foreground: 240 5% 64.9%;
    --accent: 217 91% 60%;
    --accent-foreground: 0 0% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 3.7% 15.9%;
    --input: 240 3.7% 15.9%;
    --ring: 217 91% 60%;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* React Flow Custom Styles */
.react-flow__node {
  transition:
    box-shadow 0.2s ease,
    transform 0.1s ease;
}

.react-flow__node:hover {
  z-index: 100 !important;
}

.react-flow__node.selected {
  z-index: 100 !important;
}

.react-flow__edge-path {
  stroke-width: 2;
  transition:
    stroke 0.2s ease,
    stroke-width 0.2s ease;
}

.react-flow__edge:hover .react-flow__edge-path {
  stroke-width: 3;
  filter: drop-shadow(0 0 4px currentColor);
}

.react-flow__controls {
  background: hsl(var(--card));
  border: 1px solid hsl(var(--border));
  border-radius: var(--radius);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.react-flow__controls-button {
  background: hsl(var(--card));
  border-bottom: 1px solid hsl(var(--border));
  color: hsl(var(--foreground));
}

.react-flow__controls-button:hover {
  background: hsl(var(--secondary));
}

.react-flow__minimap {
  background: hsl(var(--card));
  border: 1px solid hsl(var(--border));
  border-radius: var(--radius);
}

.react-flow__background {
  background-color: hsl(var(--background));
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: hsl(var(--muted-foreground) / 0.2);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: hsl(var(--muted-foreground) / 0.4);
}

/* Glass effect */
.glass {
  background: hsl(var(--card) / 0.8);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
}

/* Animation utilities */
@keyframes slideInRight {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 8px rgba(16, 185, 129, 0.4);
  }
  50% {
    box-shadow: 0 0 16px rgba(16, 185, 129, 0.7);
  }
}

.animate-slide-in-right {
  animation: slideInRight 0.3s ease-out;
}

.animate-fade-in {
  animation: fadeIn 0.2s ease-out;
}

/* Node handles - small default */
.node-handle {
  width: 8px !important;
  height: 8px !important;
  background: hsl(var(--accent)) !important;
  border: none !important;
  opacity: 0;
  transition: opacity 0.2s ease;
}

/* Node handles - large for better connectivity */
.node-handle-large {
  width: 16px !important;
  height: 16px !important;
  background: hsl(var(--accent)) !important;
  border: 2px solid hsl(var(--background)) !important;
  opacity: 0.7;
  transition:
    opacity 0.2s ease,
    transform 0.2s ease;
}

.node-handle-large:hover {
  opacity: 1;
  transform: scale(1.2);
}

.react-flow__node:hover .node-handle,
.react-flow__node:hover .node-handle-large {
  opacity: 1;
}

/* Node ID badge */
.node-id-badge {
  display: inline-flex;
  align-items: center;
  padding: 1px 6px;
  font-size: 10px;
  font-weight: 600;
  font-family: "JetBrains Mono", monospace;
  border-radius: 4px;
  background: hsl(var(--accent) / 0.15);
  color: hsl(var(--accent));
}

/* Path badges */
.path-badge {
  display: inline-flex;
  padding: 1px 4px;
  font-size: 8px;
  font-weight: 700;
  border-radius: 3px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.path-badge.start {
  background: rgba(16, 185, 129, 0.2);
  color: #10b981;
}

.path-badge.end {
  background: rgba(59, 130, 246, 0.2);
  color: #3b82f6;
}

/* Path highlight */
.custom-node.path-highlight {
  animation: pulse-glow 2s ease-in-out infinite;
}

.custom-node.path-start {
  border-color: #10b981 !important;
}

.custom-node.path-end {
  border-color: #3b82f6 !important;
}

.custom-node.pathfinding-mode {
  cursor: crosshair;
}

.custom-node.pathfinding-mode:hover {
  transform: scale(1.05);
  box-shadow: 0 0 12px hsl(var(--accent) / 0.5);
}

/* Connection mode */
.custom-node.connection-mode {
  cursor: crosshair;
}

.custom-node.connection-source {
  animation: pulse-amber 2s ease-in-out infinite;
}

@keyframes pulse-amber {
  0%,
  100% {
    box-shadow: 0 0 8px rgba(245, 158, 11, 0.4);
  }
  50% {
    box-shadow: 0 0 16px rgba(245, 158, 11, 0.7);
  }
}

/* New item indicator */
.custom-node .absolute {
  z-index: 10;
}

/* Filter selected items */
.view-list-item.filter-selected {
  background: rgba(59, 130, 246, 0.15) !important;
  border-left-color: #3b82f6 !important;
}

/* Suppress ResizeObserver errors */
.resize-observer-error-suppressed {
  display: none;
}
